.Addbtn{
    width: 40px;
    height: 40px;
    font-size: 20px;
    margin: 2.5vw 5vw;
    cursor:pointer;
    transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.4s;
    background-color: #F0A04B;
}
.Addbtn:focus, .Addbtn{
    outline: none;
    border: none;
}
.Addbtn:hover{    background-color: #c9863e;}

.username{
    font-size: 25px;
    color: #000000;
}
.preview{
    margin: auto;
    width: 100%;
    height: 100vh;
    background-color: #000000;
    position: fixed;
    top: 0;
    display: none;
}
.Camerabtn{
    width: 15vw;
    height: 15vw;
    background-color: #fff;
    box-shadow: #fff 0px 0px 10px;
    outline: none;
    border: none;
    border-radius: 100%;
    position: fixed;
    top: 80%;
    left: 48%;
    display: none;
    cursor: pointer;
}
/* .Input{

} */
.card{
    display:flex;
    background-color: #E1EEDD;
    color: #000000;
}
.card img{
    width: 200px;
    height: 200px;
}
.card h2{
    margin: auto 10px;
}
@media screen and (max-width: 350px){
    .Camerabtn{
        width: 20vw;
        height: 20vw;
    }
    .card{
        display: list-item;
        list-style: none;
    }.card h2{
        margin: 10px auto;
    }
}